:root {
    --pf: 'Source Sans Pro', sans-serif;
    --transition: all .5s;
    --bg: #182227;
    --bg-light: rgba(255, 255, 255, .15);
    --clr-text: #FCE8Ec;
    --clr-text-light: #FCE8Ecbb;
    --clr-element: #FFABC2;
    --clr-element-hover: #FFABC2bb;
    --clr-shadow: rgba(0, 0, 0, .2);
}
header {
    background-color: #34495e;
    display: flex;
    justify-content: space-between;
}

header h1 {
    font-size: 70px;
    color: whitesmoke;
    margin-left: 15px;
    border-bottom: none;
}
header h2 {
    font-size: 35px;
    color: aqua;

}
header img {
    max-width: 300px;
    max-height: 300px;
    margin: 10px;
}

@media all and (max-width: 850px) {
    header img {
        max-width: 100px;
        min-height: 100px;
    }
}